import { useState, useEffect, useCallback } from 'react';
import { getImageBaseUrl, getChatData } from '../database/api';
import { MessageItem, DateItem } from './MessageItem';
import { MessageText, Messages } from './MessageCss';
import $ from 'jquery';
import { connect } from 'react-redux';
import { set, changeCount, setScroll } from '../modules/chat';

const MessageController = ({ chatHistory, count, set, changeCount }) => {
  console.log('messageController');

  const load = useCallback((data, cut) => {
    let historys = data.slice(-1 * cut, -1);
    let result = new Map();

    for (let history of historys) {
      let arr = result.get(history.date) == null ? new Array() : result.get(history.date);
      arr.push(history);
      result.set(history.date, arr);
    }

    return [...result];
  }, []);

  const [imageBaseUrl, setImageBaseUrl] = useState('');
  let showHistory = load(chatHistory, count);
  let scrollHeight = $('#messages').prop('scrollHeight');

  useEffect(() => {
    const binding = async () => {
      await getImageBaseUrl('images').then((url) => {
        setImageBaseUrl(url);
      });

      await getChatData()
        .then((historys) => {
          let lastName = '';
          let firstFile = 0;
          let cnt = 1;

          for (let i = 0; i < historys.length; i++) {
            if (historys[i].message !== '(사진)') continue;

            let filename = historys[i].date.replaceAll('/', '');

            if (lastName === historys[i].date) {
              if (cnt === 1) historys[firstFile].fileName = `${filename} (${cnt})`;

              cnt++;
              historys[i].fileName = `${filename} (${cnt})`;
            } else {
              lastName = historys[i].date;
              cnt = 1;
              firstFile = i;
              historys[i].fileName = filename;
            }
          }

          set(historys);
          changeCount();

          return historys;
        })
        .then((historys) => {
          load(historys, 20);
        });
    };
    binding();
  }, []);

  useEffect(() => {
    $('#messages').scrollTop(
      scrollHeight === 0 ? $('#messages').prop('scrollHeight') : $('#messages').prop('scrollHeight') - scrollHeight
    );
  }, [showHistory]);

  const handleScroll = (event) => {
    if (event.currentTarget.scrollTop === 0) {
      changeCount();
    }
  };

  return (
    <Messages id="messages" onScroll={handleScroll}>
      {showHistory.map((element) => {
        return (
          <div key={element[0]}>
            <DateItem date={element[0]}></DateItem>
            {element[1].map((item, index) => {
              return <MessageItem params={item} imageUrl={imageBaseUrl} key={index}></MessageItem>;
            })}
          </div>
        );
      })}
    </Messages>
  );
};

export default connect(
  ({ chats }) => ({
    chatHistory: chats.chatHistory,
    count: chats.count,
  }),
  {
    set,
    changeCount,
  }
)(MessageController);
