import styled, { css } from 'styled-components';

export const Messages = styled.div(
  css`
    overflow: scroll;
    flex-grow: 1;

    display: flex;
    flex-direction: column;

    font-size: 15px;
    margin-top: 5px;

    &::-webkit-scrollbar {
      width: 4px;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 2px;
      background: #ccc;
    }
  `
);

export const Message = styled.div(
  css`
    display: flex;
    flex-direction: row;
    padding: 10px;

    margin: 10px 0;
  `
);

export const Profile = styled.img(
  css`
    width: 45px;
    height: 45px;
    flex-shrink: 0;

    margin-right: 10px;
    margin-top: 5px;
    border-radius: 100px;

    background: black;

    object-fit: cover;
  `
);

export const DateBox = styled.div(
  css`
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    border-top: 1px solid #d8d8d8;

    color: #848484;
  `
);

export const DateText = styled.span(
  css`
    text-align: center;

    margin: 10px 0px;
  `
);

export const Name = styled.div(
  css`
    margin: 0;
  `
);

export const MessageText = styled.div(
  css`
    white-space: pre-wrap;
    background: white;

    margin: 5px 0;
    padding: 10px 15px;

    border-radius: 5px 15px 15px 15px;
  `
);

export const MessageImage = styled.img(
  css`
    width: min(250px, 50vw);
    object-fit: cover;

    cursor: pointer;

    border-radius: 0px 15px 15px 15px;
  `
);

export const TimeBox = styled.div(
  css`
    display: flex;
    align-items: end;

    margin-left: 10px;
    padding-bottom: 10px;

    span {
      white-space: nowrap;
      color: #a6a6a6;
      font-size: 13px;
    }
  `
);
